import React, { Component } from 'react';

export default class VolumeController extends Component {
  
  render() {
    
    let volume = this.props.volume;
    if(volume === 10){
      volume ="Max";
    }else if(volume === 0){
      volume = "Mute";
    }
    return (
      <div className="row">
        <div className="col-xs-4">
          <span className="fa fa-stack fa-lg fa-3x" onClick={this.props.onVolumeDecrease}>
            <i className="fa fa-stack-2x fa-circle-o" />
            <span className="fa fa-stack-1x fa-minus" />
          </span>
        </div>
        <div className="col-xs-4 text-center">
          <span className="fa fa-stack fa-lg fa-3x" onClick={this.props.onVolumeDecrease}>
            <i className="fa fa-stack-2x" />
            <span className="fa fa-stack-1x" >{volume}</span>
          </span>
        </div>
        <div className="col-xs-4">
          <span className="fa fa-stack fa-lg fa-3x" onClick={this.props.onVolumeIncrease}>
            <i className="fa fa-stack-2x fa-circle-o" />
            <span className="fa fa-stack-1x fa-plus" />
          </span>
        </div>
      </div>
    );
  }
}


