import React, { Component } from 'react';

export default class ChannelController extends Component{
  constructor(props){
    super(props);
    this.state={
      isPlaying:1
    };
    this.onPause = this.onPause.bind(this);
  }

  onPause(){
    let newState = this.state.isPlaying === 1?0:1;
    console.log("newstate0: " + this.state.isPlaying);
    this.setState({isPlaying:newState});
    console.log("newstate: " + newState);
    console.log("newstate2: " + this.state.isPlaying);
    this.props.onPause(newState);
  }

  render(){
      let playIcon = "fa-pause";
      if(this.state.isPlaying == 0){
          playIcon = "fa-play";
      }
    return (
      <div className="row">
        <div className="col-xs-4">
          <span className="fa fa-stack fa-lg fa-3x" onClick={this.props.onPrevChannel}>
            <i className="fa fa-stack-2x fa-circle-o" />
            <span className="fa fa-stack-1x fa-step-backward"/>
          </span>
        </div>
        <div className="col-xs-4">
          <span className="fa fa-stack fa-lg fa-3x" onClick={this.onPause}>
            <i className="fa fa-stack-2x" />
            <span className={"fa fa-stack-1x " + playIcon}/>
          </span>
        </div>
        <div className="col-xs-4">
          <span className="fa fa-stack fa-lg fa-3x" onClick={this.props.onNextChannel}>
            <i className="fa fa-stack-2x fa-circle-o" />
            <span className="fa fa-stack-1x fa-step-forward" />
          </span>
        </div>
      </div>
    );
  }
}


