class RadioPlayer extends Component {
  render() {
    let volume = this.props.volume/10;
    let urls = this.props.channel.urls;
    return (
      <audio volume={volume} autoPlay>
        {urls.map(url=>
          <source src={url}/>
        )}
      </audio>
    );
  }
}

